import {CCard, CCardBody, CRow} from "@coreui/react";
import CountUp from "react-countup";
import Button from "react-bootstrap/Button";
import Table from "react-bootstrap/Table";
import React, {useEffect, useState} from "react";
import {getCookieValue, ipAddress, tokenName} from "../../utils/Common";
import axios from "axios";
import {Link} from "react-router-dom";

const FlightsViewMarkup = () => {
  const [markupData, setMarkupData] = useState([]);
  useEffect(() => {
    getMarkupData();
  }, []);

  function getMarkupData() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/markupPlans", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    }).then((response) => {
      console.log(response);
      setMarkupData(response.data.recordset);
    }).catch(e => {
      console.log(e)

    })
  }

  return (
    <>
      <CCard className="mb-4">
        <CCardBody>
          <CRow>
            <div className="table-responsive px-3 ">
              <h2 style={{textAlign: 'center'}}><b>Already Added Markups : </b></h2>
              <hr/>
              <div className="bg-light py-2 mb-3">
                <Link to="/flightmarkups">
                  <Button variant="primary">
                    Add New Markups
                  </Button>
                </Link>
                <div className="row">

                </div>

              </div>

              <div className="row">

                <br/>

                <div style={{height: "335px"}} className="table-responsive">
                  <Table responsive hover>
                    <thead style={{position: "sticky", top: 0}}>
                    <tr>
                      <th>ID</th>
                      <th>Carrier</th>
                      <th>Fare Type</th>
                      <th>Supplier</th>
                      <th>Type</th>
                      <th className="text-center">Markup Value</th>
                      <th className="text-center">Cancellation Charge</th>
                      <th className="text-center">Rescheduling Charge</th>
                    </tr>
                    </thead>

                    <tbody>
                    {markupData?.length > 0 && markupData.map((item, index) => (
                      <tr key={index}>
                        <td> {index + 1} </td>
                        <td>{item.carrierName}</td>
                        <td>{item.fare_type}</td>
                        <td>{item.supplier}</td>
                        <td>{item.type}</td>
                        <td className="text-center">{item.markup_value}</td>
                        <td className="text-center">{item.cancellation_charges}</td>
                        <td className="text-center">{item.rescheduling_charges}</td>
                      </tr>
                    ))
                    }

                    </tbody>


                  </Table>

                </div>

              </div>

            </div>
          </CRow>
        </CCardBody>
      </CCard>
    </>
  )
}

export default FlightsViewMarkup
